/* Sidebar Styles */
.sidebar {
  width: var(--sidebar-width);
  background-color: #0e0e0e;
  border-right: 1px solid var(--border-color);
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.logo {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  color: var(--primary-color);
}

.logo svg {
  margin-right: 10px;
}

.new-question-btn {
  background-color: var(--primary-color);
  color: white;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  margin-bottom: 20px;
}

.new-question-btn svg {
  margin-right: 8px;
}

.shortcut {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 2px 5px;
  border-radius: 3px;
  font-size: 12px;
}

.sidebar-menu {
  margin-top: 20px;
}

.menu-item {
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.menu-item svg {
  margin-right: 10px;
  width: 20px;
  text-align: center;
}

.menu-item:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.menu-item.active {
  background-color: rgba(30, 136, 229, 0.1);
  color: var(--primary-color);
}

.badge {
  background-color: #f44336;
  color: white;
  font-size: 12px;
  padding: 2px 6px;
  border-radius: 10px;
  margin-left: auto;
} 