/* Chat Container Styles */
.chat-container {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.question-display {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 20px;
}

.card {
  background-color: var(--card-background);
  border-radius: 8px;
  padding: 15px;
  border: 1px solid var(--border-color);
}

.card-title {
  font-weight: bold;
  margin-bottom: 10px;
  color: rgba(255, 255, 255, 0.7);
  display: flex;
  align-items: center;
}

.card-title svg {
  margin-right: 8px;
}

.card-content {
  font-size: 16px;
  line-height: 1.5;
}

.sources-container {
  margin-top: 20px;
}

.sources-heading {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 10px;
}

.sources-relevance {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 15px;
}

.sources-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.source-card {
  background-color: var(--card-background);
  border-radius: 8px;
  padding: 15px;
  border: 1px solid var(--border-color);
}

.source-number {
  font-weight: bold;
  margin-bottom: 5px;
}

.source-ids, .source-time, .source-tokens {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 5px;
}

.source-content {
  margin-top: 10px;
  font-size: 14px;
  line-height: 1.5;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 10px;
  border-radius: 5px;
  border-left: 3px solid var(--primary-color);
}

/* File Upload Status */
.upload-status {
  margin-bottom: 20px;
}

.file-status {
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 14px;
}

.file-status.success {
  background-color: rgba(76, 175, 80, 0.2);
  border: 1px solid rgba(76, 175, 80, 0.5);
}

.file-status.error {
  background-color: rgba(244, 67, 54, 0.2);
  border: 1px solid rgba(244, 67, 54, 0.5);
} 