/* Main Content Styles */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
}

/* Processing Indicator */
.processing {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
}

.processing-text {
  margin-right: 10px;
}

.dots {
  display: flex;
}

.dot {
  width: 8px;
  height: 8px;
  background-color: var(--primary-color);
  border-radius: 50%;
  margin: 0 3px;
  animation: dot-pulse 1.5s infinite ease-in-out;
}

.dot:nth-child(2) {
  animation-delay: 0.2s;
}

.dot:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes dot-pulse {
  0%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  50% {
    transform: scale(1.2);
    opacity: 1;
  }
}

/* Sentence styles */
.sentence {
  margin-bottom: 8px;
  padding-left: 5px;
}

.loading {
  color: var(--primary-color);
  font-style: italic;
}

.error {
  color: #f44336;
  font-style: italic;
} 